input[type="email"].styler,
input[type="number"].styler,
input[type="password"].styler,
input[type="search"].styler,
input[type="tel"].styler,
input[type="text"].styler,
input[type="url"].styler,
textarea.styler {
  padding: 7px 9px;
  border: 1px solid #CCC;
  border-bottom-color: #B3B3B3;
  border-radius: 4px;
  box-shadow: inset 1px 1px #F1F1F1, 0 1px 2px rgba(0, 0, 0, 0.1);
  color: #333;
  font: 14px Arial, sans-serif;
}

input[type="search"].styler {
  -webkit-appearance: none;
}

textarea.styler {
  overflow: auto;
}

input[type="email"].styler:hover,
input[type="number"].styler:hover,
input[type="password"].styler:hover,
input[type="search"].styler:hover,
input[type="tel"].styler:hover,
input[type="text"].styler:hover,
input[type="url"].styler:hover,
textarea.styler:hover {
  border-color: #B3B3B3;
}

input[type="email"].styler:focus,
input[type="number"].styler:focus,
input[type="password"].styler:focus,
input[type="search"].styler:focus,
input[type="tel"].styler:focus,
input[type="text"].styler:focus,
input[type="url"].styler:focus,
textarea.styler:focus {
  border-color: #CCC;
  border-top-color: #B3B3B3;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
  outline: none;
}

button.styler,
input[type="button"].styler,
input[type="submit"].styler,
input[type="reset"].styler {
  overflow: visible;
  padding: 7px 11px;
  outline: none;
  border: 1px solid #CCC;
  border-bottom-color: #B3B3B3;
  border-radius: 4px;
  background: #F1F1F1 url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAAeCAIAAABi9+OQAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAABdJREFUeNpi+v//PxM2zMDAQEtxgAADAF2uTe9L4getAAAAAElFTkSuQmCC) repeat-x;
  box-shadow: inset 1px -1px #F1F1F1, inset -1px 0 #F1F1F1, 0 1px 2px rgba(0, 0, 0, 0.1);
  color: #333;
  text-shadow: 1px 1px #FFF;
  font: 14px Arial, sans-serif;
  cursor: pointer;
}

button.styler::-moz-focus-inner,
input[type="button"].styler::-moz-focus-inner,
input[type="submit"].styler::-moz-focus-inner,
input[type="reset"].styler::-moz-focus-inner {
  padding: 0;
  border: 0;
}

button.styler:after {
  content: '';
}

button.styler:not([disabled]):hover,
input[type="button"].styler:not([disabled]):hover,
input[type="submit"].styler:not([disabled]):hover,
input[type="reset"].styler:hover {
  background-color: #E6E6E6;
  background-position: 0 -10px;
}

button.styler:not([disabled]):active,
input[type="button"].styler:not([disabled]):active,
input[type="submit"].styler:not([disabled]):active,
input[type="reset"].styler:active {
  background: #F5F5F5;
  box-shadow: inset 1px 1px 3px #DDD;
}

button.styler[disabled],
input[type="button"].styler[disabled],
input[type="submit"].styler[disabled] {
  border-color: #CCC;
  background: #F5F5F5;
  box-shadow: none;
  color: #888;
}

/**
* @section Form
*/
.l-form {
  margin-top: 150px;
}

.b-form {
  position: relative;
  margin: 0 auto;
  padding: 40px 50px 50px;
  width: 327px;
  border: 1px solid #e9e9e9;
  border-radius: 5px;
  box-shadow: 0 1px 0 #dbdbdb;
  text-align: center;
  font: 13px 'Open Sans', sans-serif;
}
.b-form__controls {
  margin-top: 20px;
  text-align: left;
}
.b-form__controls:first-child {
  margin-top: 0;
}
.b-form__field {
  box-sizing: border-box;
  display: block;
  margin: 0;
  padding: 0 15px;
  width: 100%;
  height: 45px;
  border: 1px solid #dedede;
  border-radius: 5px;
  background: #fff;
  font-size: 14px;
}
.b-form__field:focus {
  outline: none;
  border-color: #6aaddb;
  box-shadow: 0 0 20px #6aaddb;
  box-shadow: 0 0 20px rgba(106, 173, 219, 0.3);
}
.b-form__link {
  display: inline-block;
  margin-top: 20px;
  color: #3c90be;
  text-decoration: none;
}
.b-form__link:hover {
  text-decoration: underline;
}
.b-form__button {
  display: block;
  width: 100%;
  height: 47px;
  margin: 0;
  border: none;
  border-radius: 5px;
  background-color: #3c90be;
  color: #fff;
  text-align: center;
  text-decoration: none;
  font: 600 16px/47px 'Open Sans', sans-serif;
  cursor: pointer;
  transition: background-color .2s;
}
.b-form__button:hover {
  background-color: #327fa8;
}
.b-form__button.-color_light {
  background-color: #ebf4f8;
  color: #1a171b;
}
.b-form__button.-color_light:hover {
  background-color: #b6d6e5;
}
.b-form__footer {
  margin-top: 24px;
  padding-top: 25px;
  border-top: 1px solid #ededed;
}

